@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&display=swap);
#root {
  font-size: 0;
  position: relative;

  --sidebar-width: 26rem;
}

#sidebar,
#svgRoot {
  height: 100%;
  font-size: 1rem;
}

#sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: var(--sidebar-width);
  background-color: hsl(0, 0%, 97.5%);
  padding: 0.625rem;
  box-shadow: 0 1.25rem 0.625rem rgba(0, 0, 0, 0.19), 0 0.5rem 0.5rem rgba(0, 0, 0, 0.23);
  z-index: 1;
  overflow: hidden;
  overflow-y: auto;
}

#sidebar > footer {
  text-align: center;
  margin-bottom: 2rem;
}

#sidebar > footer > .byLine {
  margin-top: 1rem;
  font-size: 0.8rem;
}

#sidebar > footer a {
  display: inline-block;
  padding: 0.75rem;
  color: #3f51b5;
  text-decoration: none;
}

#sidebar > footer > .byLine a {
  padding: 0;
  font-size: 0.8rem;
}

#sidebar > footer a:hover,
#sidebar > footer a:active {
  text-decoration: underline;
}

h1 {
  text-align: center;
  margin: 2.5rem 0;
  font-size: 2.25rem;
  font-weight: 300;
  color: hsl(0, 0%, 25%);
  letter-spacing: -0.035em;
}

#form {
  color: hsl(0, 0%, 15%);
}

fieldset {
  padding: 2rem 1.25rem;
  margin-bottom: 2.25rem;
  border-color: hsl(0, 0%, 95%);
}

legend {
  padding: 0 0.625rem;
  margin-left: -0.625rem;
  font-size: 1.2em;
}

.formField {
  margin-bottom: 0.625rem;
}

input[type="number"],
input[type="range"],
select {
  width: 100%;
  padding: 0.375rem;
}

input[type="color"] {
  width: 100%;
  height: 2.5rem;
}

.columns {
  display: flex;
}

.columns .formField {
  width: calc(50% - 1rem);
}

.columns .formField:first-child {
  margin-right: 1rem;
}

.columns .formField:last-child {
  margin-left: 1rem;
}

.spot,
.stop {
  border: 1px solid hsl(0, 0%, 85%);
  margin: 1.25rem 0;
  padding: 1.25rem;
  background-color: hsl(0, 0%, 90%);
  position: relative;
}

label {
  display: inline-block;
  padding-bottom: 0.2rem;
}

label.big {
  font-size: 1.25em;
  margin-bottom: 0.625rem;
  display: inline-block;
}

.form-coloring-gradient-remove-spot,
.form-coloring-gradient-remove-stop {
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  width: 2rem;
  height: 2rem;
  padding: 0;
  background-color: hsl(0, 0%, 90%);
  border: 0;
  font-size: 1.25rem;
}

button {
  padding: 0.625rem;
  cursor: pointer;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 0.75px;
}

#svgRoot {
  position: absolute;
  width: calc(100% - var(--sidebar-width));
  top: 0;
  left: var(--sidebar-width);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: hsl(0, 0%, 90%);
  overflow: auto;
}

#svgRoot::-webkit-scrollbar-track {
  background-color: hsl(0, 0%, 90%);
}

#output {
  font-size: 0;
  box-shadow: 0 0 2rem hsl(0, 0%, 75%);
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAABmJLR0QA/wD/AP+gvaeTAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAB3RJTUUH5AgJFBoEoBbCEQAAABl0RVh0Q29tbWVudABDcmVhdGVkIHdpdGggR0lNUFeBDhcAAAArSURBVDjLY0xLS/vPgAcYGxvjk2ZgYqAQjBowGAxgIRTPZ8+eHQ3E4W8AAEktBewUY/QYAAAAAElFTkSuQmCC');
}

.presets {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.preset {
  border: 0;
  height: 3.75rem;
  width: calc(33.3333% - 1.25rem);
  margin: 0.625rem;
}

#downloadSvgButton {
  position: fixed;
  bottom: 1.25rem;
  right: 1.25rem;
  width: auto;
  padding: 1.25rem 2rem;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: none;
}

/* Set margin, padding to 0, and box sizing to `border-box` for all elements */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

/* Default height for the <html> and <body> elements and the application root */
html,
body,
#root {
  height: 100%;
}

/* Nice scrollbars */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background-color: hsl(0, 0%, 97.5%);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: hsl(0, 0%, 70%);
}

html {
  font-size: 16px;
}

@media (max-width: 1366px) {
  html {
    font-size: 15px;
  }
}

@media (max-width: 1196px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 1024px) {
  html {
    font-size: 13px;
  }
}

/* Body defaults */
body {
  font-family:
    Poppins,
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  scroll-behavior: smooth;
  line-height: 1;
  box-sizing: border-box;

  /*
    Font-smoothing is a non-standard feature. Read more:
    https://developer.mozilla.org/en-US/docs/Web/CSS/font-smooth
  */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Make images fit in their parent elements horizontally */
img {
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Set monospace font for <code> elements */
code {
  font-family:
    source-code-pro,
    Menlo,
    Monaco,
    Consolas,
    'Courier New',
    monospace;
}

